.Toggle input[type="checkbox"] {
  position: absolute;
  left: -100vw;
}

.Toggle input[type="checkbox"] + label {
  cursor: pointer;
  white-space: nowrap;
  line-height: 3rem;
  display: block;
  position: relative;
}

.Toggle input[type="checkbox"] + label:before, .Toggle input[type="checkbox"] + label:after {
  content: "";
  transition: all .5s;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.Toggle__large input[type="checkbox"] + label {
  font-size: 2rem;
  line-height: 6rem;
}

.Toggle input[type="checkbox"] + label {
  padding-left: 5rem;
}

.Toggle input[type="checkbox"] + label:before {
  aspect-ratio: 2;
  background: #eee;
  border-radius: 1rem;
  width: 4rem;
}

.Toggle input[type="checkbox"]:checked + label:before {
  background: #ddf8eb;
}

.Toggle input[type="checkbox"] + label:after {
  width: 1.4rem;
  left: .25rem;
}

.Toggle input[type="checkbox"]:checked + label:after {
  animation: .5s linear toggle;
  transform: translate(125%, -50%);
}

.Toggle__large input[type="checkbox"] + label {
  padding-left: 9rem;
}

.Toggle__large input[type="checkbox"] + label:before {
  border-radius: 2rem;
  width: 8rem;
}

.Toggle__large input[type="checkbox"] + label:after {
  width: 3rem;
  left: .5rem;
}

.Toggle input[type="checkbox"] + label:after {
  aspect-ratio: 1;
  background: radial-gradient(circle at 70% 30%, #fff, #0000 25%), radial-gradient(circle at 60% 55%, #0000 60%, #f0fc 100%), radial-gradient(circle, #0000 40%, #0ff3 60%, #0000 68%), radial-gradient(circle at 50% 55%, #0000 35%, #ff03 45%, #0000 55%);
  border: .1rem solid #fff;
  border-radius: 50%;
}

@keyframes toggle {
  0% {
    transform: translate(0, -50%);
  }

  25% {
    transform: translate(32%, -75%);
  }

  35% {
    transform: translate(44.8%, -75%);
  }

  65% {
    transform: translate(84.06%, -40%);
  }

  75% {
    transform: translate(96%, -40%);
  }

  100% {
    transform: translate(125%, -50%);
  }
}
